@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Birthstone&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Helvetica Neue', 'Noto Sans','Verdana', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  background: url(../../static/media/main.fa58bf9e.png);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.component {
  /*background-color: rgba(0, 0, 200, 0.4);*/
  height: 100vh;
  width: 100vw;
}


.welcome__entering {
  position: absolute;
  top: 150%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}


.welcome {
  transition: 1s;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

.welcome__exiting {
  transition: 1s;
  position: absolute;
  top: -50%;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}


.welcome__text {
/*  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);*/
  text-align: center;
}

.welcome__title {
  /*font-size: 1.5em;*/
  color: #fff;
}

.welcome__description {
  margin-top: 1rem;
  /*font-size: 1.25em;*/
  font-weight: bolder;
  color: rgba(225, 225, 225, 0.9);
}

.welcome__start-button {
  /*font-size: 1.4rem;*/
  display: block;
  font-weight: bold;
  background-color: #000;
  color: #fff;
/*  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translate(-50%);*/
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 1rem 2rem;
  margin: 2rem auto;
}

/*.welcome__start-button:active {
  border: none;
  outline: none;
}*/

.start-button-clicked {
  background-color: #555;
  color: orange;
}

