}

.error-text-wrapper {
  color: red;
  height: 4rem;
  width: 80vw;
  margin: 0 auto;
  margin-top: 5vh;
  /*margin-bottom: 3vh;*/
  background-color: rgba(200, 0, 0, 0.3);
  border-radius: 5px;
}

.error-text {
  padding: 1.5rem;
}

.checkmark {
  height: 10vh;
  border-radius: 50%;
  display: block;
  stroke-width: 5;
  stroke: white;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-color: rgba(225, 225, 225, 0.5);
  border: 1px solid #fff;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 5;
  stroke-miterlimit: 10;
  stroke: gray;
  fill: none;
}

